.header__container,
div[class*='__container'] {
  max-width: 1174px ;
  margin: 0 auto;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
ul {
  margin: 0;
  padding: 0;
}
body {
  font-family: 'M PLUS 1p', 'PT Sans', sans-serif;
  color: #30233D;
}
html,
body {
  height: 100%;
  line-height: 1;
  margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}
.title {
  font-style: normal;
  font-weight: 500;
  font-size: 54px;
  line-height: 80px;
}
.subtitle {
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 37px;
  letter-spacing: 4px;
  text-transform: uppercase;
}
.header__logo {
  font-weight: 900;
  font-size: 24px;
  line-height: 58px;
  letter-spacing: 2px;
  color: #30233D;
  flex: 1 0 auto;
}
.navigation__link,
.header-contacts {
  font-weight: 800;
  font-size: 14.4px;
  line-height: 30px;
  letter-spacing: 2px;
  color: #30233D;
}
.navigation__list {
  display: flex;
  width: 460px;
  padding-top: 1em;
}
.navigation__item:not(:last-child) {
  padding-right: 54px;
}
.header__container {
  display: flex;
  padding-top: 20px;
  padding-bottom: 20px;
}
.header-contacts {
  position: relative;
  flex: 0 1 320px;
  padding-top: 1em;
}
.header-contacts::before {
  content: '';
  position: absolute;
  width: 18px;
  height: 29px;
  background: url('../images/phone.SVG') no-repeat 0px 0;
  transform: translate(-32px, 0px);
}
.menu__button {
  height: 16px;
  width: 27px;
  position: relative;
  transform: translate(10px, 20px);
}
.menu__button::before {
  content: '';
  background-color: #30233D;
  position: absolute;
  bottom: 2px;
  right: 0;
  width: 18px;
  height: 1px;
}
.menu__button::after {
  content: '';
  background-color: #30233D;
  position: absolute;
  width: 27px;
  height: 2px;
  box-shadow: 0 6.6px 0 0 #30233D;
  bottom: 15px;
}
